import React from "react";
import classnames from "classnames";
// import style from "./button.st.css";

import { st, classes } from "./button.st.css";

import PropTypes from "prop-types";

/**
 * Button props extending those of a regular button.
 */
export interface ButtonProps
  extends Pick<
    React.ButtonHTMLAttributes<HTMLButtonElement>,
    Exclude<keyof React.ButtonHTMLAttributes<HTMLButtonElement>, "color">
  > {
  /** Type of the button default, primary..*/
  color?: false | 1 | 2 | 3 | 4 | 5;
  /** Optional ref. */
  ref?: React.Ref<HTMLButtonElement>;
  disabled?: boolean;
  /** Button variant. */
  variant?: false | 1 | 2 | 3 | 4 | 5;
  /** Extra text that can be used to render a tooltip on hover/focus. */
  tip?: string;
  uppercase?: boolean;
  accent?: false | 1 | 2 | 3 | 4 | 5;
  /** Button sizes:  */
  vol?: false | 1 | 2 | 3 | 4 | 5;
}

const Button = React.forwardRef(
  (
    {
      children,
      className: classNameProp,
      disabled,
      tip,
      color,
      vol = 3,
      uppercase,
      variant,
      ...attrs
    }: ButtonProps,
    ref?: React.Ref<HTMLButtonElement>
  ) => {
    const rootClassNames = classnames(
      classes.root,
      classes["vol" + vol],
      // classes["variant" + variant],
      // classes[color],
      // classes[variant],
      classNameProp
    );

    return (
      <button
        {...attrs}
        className={st(rootClassNames, { variant, color, disabled, uppercase })}
        ref={ref}
      >
        <span className={classes.inner}>{children}</span>
        <span className={classes.inner2}>test</span>
        {tip && <span className={classes.tip}>{tip}</span>}
      </button>
    );
  }
);

Button.displayName = "Button";
Button.propTypes = {
  style: PropTypes.object
};

export default Button;
export const proptype = Button.propTypes;
